/* Phonebook */

body {
  font-family: 'Roboto', sans-serif;
  background: url(/phonebook-challenge/static/media/background.82663453.jpg) no-repeat;
  background-size: cover;
  /* Attaches images to viewport */
  background-attachment: fixed;
}

.container {
  padding: 0px 40px;
  height: 100%;
}

@media(max-width: 650px) {
  .container {
    padding: 0px 10px;
  }
}

@media(max-width: 500px) {
