.box {
  /* Width percentage was used for a more responsive behaviour */
  width: 85%;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  background-color: #eee;
  border-radius: 4px;
  margin-bottom: 15px;
  /* Shadow is added to the boxes */
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
}

@media(max-width: 500px) {
  .box {
    width: 94% !important;
  }
}

.box h2 {
  font: bold 26px 'Roboto';
  padding-top: 30px;
  color: rgb(90, 90, 90);
}

.box h2 i {
  color: #345AD9;
}

